import React from 'react'
import { Gitgraph, Orientation, templateExtend, TemplateName } from '@gitgraph/react'

import patients from './data'

function createFixedHashGenerator() {
  let hashIndex = 1
  return () => `#${hashIndex++}`
}

const withoutBranchLabels = templateExtend(TemplateName.Metro, {
  branch: { label: { display: false } }
})

function App() {
  const renderMessage = (commit) => {
    const body = commit.body

    let color
    if (body.Sembuh) color = '#497d09'
    else if (body.Meninggal) color = '#ff3d09'
    else color = '#497dff'

    const message = (

      <p style={{ lineHeight: 1.25 }}>
        {body.JK === 'P' ? <>Pasien <b>perempuan</b></> : body.JK === 'L' ? <>Pasien <b>lelaki</b></> : <>Seorang pasien </>}
        {body.Usia !== '?' ? <> berusia <b>{body.Usia}</b> tahun</> : ''}
        {body.WN === 'ID' ? <> dengan kewarganegaraan <b>Indonesia</b> </> : body.WN === 'WNA' ? <> dengan kewarganegaraan <b>asing</b> </> : ''}
        {body.Kota ? <> asal <b>{body.Kota}</b> </> : body.Provinsi ? <>asal <b>{body.Provinsi}</b> </> : ''}
        {body['Dirawat di'] ? <> telah dirawat di <b>{body['Dirawat di']}</b>.</> : <> telah dilaporkan positif COVID19.</>}
        {body.Sembuh ? <> Pasien dilaporkan telah <b style={{ color: color }}>sembuh</b> pada <b>{body.Sembuh}</b>.</> : ''}
        {body.Meninggal ? <> Pasien dilaporkan <b style={{ color: color }}>meninggal</b> pada <b>{body.Meninggal}</b>.</> : ''}
        {body['Kondisi Kesehatan'] ? <><br /><small style={{ color: 'grey' }}>{body['Kondisi Kesehatan']}</small></> : ''}
        {body.Keterangan ? <><br /><small style={{ color: 'grey' }}>{body.Keterangan}</small></> : ''}
        {body.Referensi ? <><br /><small><a href={body.Referensi}>sumber</a></small></> : ''}

      </p>

    )

    return (
      <g transform={`translate(0, ${commit.style.dot.size})`}>
        <text fill={color} alignmentBaseline='central'>
          Kasus {commit.hashAbbrev}  ●  {body.Positif}
        </text>
        <foreignObject width='600' x='0'>
          {message}
        </foreignObject>
      </g>
    )
  }

  return (
    <div>
      <h1 style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        Linimasa Pasien Terkonfirmasi COVID-19 di Indonesia<br />
      </h1>
      <h5 style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        Dimutakhirkan pada 14 Maret 2020
      </h5>
      <h5 style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        sumber :&nbsp;<a href="https://kcov.id/daftarpositif">Daftar kasus COVID-19 di Indonesia</a>
      </h5>
      <p style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
        <Gitgraph options={{ template: withoutBranchLabels, generateCommitHash: createFixedHashGenerator() }}>
          {(gitgraph) => {
            for (const patient of patients) {
              gitgraph.commit({ body: patient, subject: '', renderMessage })
            }
          }}
        </Gitgraph>
      </p>
    </div>
  )
}

export default App
